.list {
    background-color: #993300;
    margin : 10px;
    padding : 10px;
}

.list-items {
    background-color: #ffcccc;
    margin : 10px;
    padding : 10px;
    border-radius : 5px;
    position : relative;
}

.list-items span {
    position : absolute;
    right : 15px;
    cursor : pointer;
}

.list-items input {
    background-color : transparent;
    border : 0;
    font-size: 18px;
}

.list-items input:focus {
    outline : none;
}